<template>
    <div class='xiangqing'>
        <div class="logo">
            <div class="left">
                <!-- <img src="../assets/img/shangcheng/logo.png" alt=""> -->
                <img src="../assets/img/shangcheng/logo_02.png" alt="">
            </div>
            <div class="right">
                <div class="sousuo2">
                    <div>
                        <el-input
                            :placeholder="$t('pro[0]')"
                            v-model="input"
                            clearable>
                        </el-input>
                    </div>
                    <div class="sousuosousuo" @click="sousuo()">
                        {{$t('pro[1]')}}
                    </div>
                </div>
                <div class="cen">
                    <div class="list">
                        <div>{{$t('pro[2]')}}</div>
                        <div class="item" v-for="(item,i) in hot_searches" :key="i" @click="sousuo(item)">{{item}}</div>
                    </div>
                </div>
            </div>
        </div>
        <div class="title">{{$route.query.title}}</div>
        <router-view></router-view>
    </div>
</template>
<script>
export default {
    data() {
        return {
            input:'',//搜索关键词
            hot_searches:'',//关键词
        };
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        _this.apis.get(_this.apis.hot_searches,{params:{lang:_this.apis.lang}}).then(function(res) {
            if(res.data.code == 0){
                _this.hot_searches = res.data.data
            }
        })
    },
    watch:{
    },
    methods:{
        sousuo(key){
            var _this = this;
            if(key){
                _this.$router.push({
                    path:'bai',
                    query:{title:"$t('pro[3]')",key:key}
                })
            }else{
                _this.$router.push({
                    path:'bai',
                    query:{title:"$t('pro[4]')",key:_this.input}
                })
            }
        }, 
        // sousuo(key){
        //     var _this = this;
        //     if(key){
        //         _this.apis.get(_this.apis.get_goods_list,{params:{lang:_this.apis.lang,status:1,key:key}}).then(function(res) {
        //             if(res.data.code == 0){
        //                 _this.get_goods_list = res.data.data.data
        //             }
        //         })
        //     }else{
        //         _this.apis.get(_this.apis.get_goods_list,{params:{lang:_this.apis.lang,status:1,key:_this.input}}).then(function(res) {
        //             if(res.data.code == 0){
        //                 _this.get_goods_list = res.data.data.data
        //             }
        //         })
        //     }
        // },
       
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.xiangqing{
    .logo{
        width: 1200px;
        height: 137px;
        margin: 0 auto;
        padding-top: 35px;
        display: flex;
        justify-content: left;
        align-items: top;
        .left{
            width: 42px;
            height: 65px;
            margin-left: 80px;
            margin-right: 72px;
        }
        .right{
            width: 639px;
            margin-top: 10px;
            .sousuo2{
                width: 640px;
                height: 43px;
                line-height: 43px;
                margin: 0 auto;
                margin-bottom: 10px;
                display: flex;
                background-image: linear-gradient(to right, #FFC344 , #FFAD00);
                &>div:nth-child(1){
                    width: 541px;
                    height: 39px;
                    margin-left: 2px;
                }
                &>div:nth-child(2){
                    flex: 1;
                    text-align: center;
                    font-size: 18px;
                    color: #FFFEFE;
                    font-weight: bold;
                }
            }
            .sousuosousuo{
                cursor:pointer;
            }
            .cen{
                color: #a1a1a1;
                .list{
                    margin: 0 auto;
                    padding: 0 10px;
                    display: flex;
                    justify-content: left;
                    div{
                        margin-right: 26px;
                    }
                    .item{
                        cursor:pointer;
                        margin-right:  26px;
                    }
                }
            }
        }
    }
    .title{
        font-size: 18px;
        font-weight: bold;
        color: #fff;
        height: 44px;
        line-height: 44px;
        margin: 20px 0;
        text-align: center;
        background: #FFAD01;
    }
}
</style>
<style>
    .select .el-input__icon{
        line-height: 34px;
    }
    .sousuo .el-input__inner{
        border-radius: 0px;
        border-radius: 0px !important;
    }
    .sousuo .el-input__inner:hover{
        border-radius: 0px !important;
    }
    .sousuo .el-input__inner:focus{
        border-radius: 0px !important;
    }
</style>


